.rec {
  padding: 100px 0;
  &__container {
    position: relative;
  }
  &__title {
    max-width: 160px;
    margin-bottom: 20px;
  }
  &__text {
    max-width: 280px;
  }
  @media screen and (min-width: 425px) {
    &__container {
      display: flex;
      justify-content: center;
    }
  }
  @media screen and (min-width: 1024px) {
    &__title {
      max-width: 540px;
    }
    &__text {
      max-width: 540px;
    }
  }
}

.settings {
  width: 150px;
  position: absolute;
  top: 0;
  left: 150px;
  @media screen and (min-width: 425px) {
    position: unset;
    width: 200px;
  }
  @media screen and (min-width: 1024px) {
    order: -1;
    width: 300px;
    margin-right: 100px;
  }
}
