.answer {
  padding: 100px 0;
  position: relative;
  &__bg {
    position: absolute;
    z-index: -1;
    height: 100%;
    left: 0;
    top: 50px;
  }
  &__card {
    box-shadow: 0px 4px 16px rgba(63, 146, 209, 0.12);
    border-radius: 25px;
    max-width: 420px;
    background-color: #fff;
    &:last-child {
      margin-left: auto;
    }
  }
  &__text {
    padding: 20px;
  }
  &__circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    transform: translate(-10%, -50%);
    font-weight: 900;
    font-size: 2em;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
  }
  &__pics {
    display: flex;
    width: 250px;
    margin: 0 auto;
  }
  &__plain {
    width: 50%;
    transform: translateY(-40%);
  }
  &__message {
    width: 50%;
  }
  @media screen and (min-width: 1024px) {
    &__bg {
      height: unset;
      width: 100%;
      left: unset;
      right: 0;
    }
  }
  @media screen and (min-width: 1440px) {
    padding: 200px 0;
    &__pics {
      width: 350px;
    }
  }
}

