* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.container {
  padding: 0 20px;
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    padding: 0 60px;
  }
  @media screen and (min-width: 1024px) {
    padding: 0 100px;
  }
  @media screen and (min-width: 1920px) {
    padding: 0 400px;
  }
}

.overflow {
  overflow: hidden;
}

.bg-item {
  position: absolute;
  z-index: -1;
}

.opacity {
  opacity: 0.5;
  display: none;
  @media screen and (min-width: 768px) {
    display: unset;
  }
}

//text

.text {
  font-size: 0.875em; //14px
  line-height: 1.428571428571429em; //20px
  text-align: left;
  color: $text;
  @media screen and (min-width: 1024px) {
    font-size: 1.125em; //18px
  }
}

.title {
  font-size: 1.25em; //20px
  line-height: 1.15em; //23px
  text-align: left;
  color: $title;
  font-weight: 400;
  position: relative;
  z-index: 100;
  @media screen and (min-width: 1024px) {
    font-size: 2.5em; //40px
  }
}

.title-sm {
  font-size: 1em; //16px
  line-height: 1.1875em; //19px
  text-align: left;
}

//btns

.btn {
  min-width: 120px;
  min-height: 30px;
  outline: none;
  border: 0;
  border-radius: 6px;
  padding: 5px 12px;
  font-size: 0.875em; //14px
  line-height: 1.428571428571429em; //20px
  color: #fff;
  position: relative;
  z-index: 100;
  cursor: pointer;
  @media screen and (min-width: 1024px) {
    font-size: 1.125em; //18px
  }
}

.whatsapp-btn {
  background-color: #4caf50;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    margin-bottom: unset;
    margin-right: 50px;
  }
}

.tg-btn {
  background-color: #41b4e6;
}

.social-btn {
  width: 44px;
  position: absolute;
  top: 50%;
  transform: translate(-110%, -50%);
}
