.intro {
  padding: 40px 0;
  position: relative;
  &__text-container {
    margin-bottom: 30px;
  }
  &__title {
    font-size: 2em; //32px
    font-weight: 400;
    margin-bottom: 10px;
  }
  &__btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 30px;
  }
  @media screen and (min-width: 768px) {
    &__title {
      max-width: 240px;
    }
    &__text {
      max-width: 300px;
    }
    &__btns {
      flex-direction: unset;
    }
  }
  @media screen and (min-width: 1024px) {
    &__title {
      max-width: 415px;
      font-size: 3.75em; //60px
    }
    &__text {
      max-width: 500px;
    }
  }
}

.intro-pics {
  width: 245px;
  position: absolute;
  right: -90px;
  bottom: -30px;
  &__blot {
    position: absolute;
    z-index: -1;
    width: 100%;
  }
  &__phone {
    position: relative;
    width: 40%;
    left: 25%;
    z-index: 10;
  }
  &__ru {
    position: absolute;
    width: 10%;
    left: 30px;
    top: 35%;
  }
  &__uk {
    position: absolute;
    width: 10%;
    left: 20px;
    top: 60%;
  }
  &__messages {
    position: absolute;
    width: 50%;
    right: 0;
    top: 20px;
  }
  @media screen and (min-width: 768px) {
    width: 400px;
    right: -30px;
  }
  @media screen and (min-width: 1024px) {
    width: 530px;
    right: -100px;
  }
  @media screen and (min-width: 1440px) {
    width: 530px;
    right: 15%;
  }
}

.intro-cross1 {
  width: 70px;
  left: 20%;
  top: 0;
}
.intro-cross2 {
  left: 50%;
  top: 30%;
}
.intro-cross3 {
  left: 10%;
  top: 50%;
  width: 30px;
}
.intro-cross4 {
  left: 60%;
  top: 70%;
}
.intro-cross5 {
  right: 10%;
  top: 20%;
}
