.header {
  padding: 10px 0;
  font-size: 0.875em; //14px
  color: $title;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }
  &__qa {
    margin-right: 27px;
  }
  &__blot-left {
    position: absolute;
    width: 200px;
    opacity: 0.3;
    left: -100px;
    z-index: -1;
  }
  &__blot-right {
    position: absolute;
    z-index: -1;
    opacity: 0.3;
    transform: rotate(160deg);
    width: 200px;
    right: -100px;
    top: -100px;
  }
  @media screen and (min-width: 768px) {
    &__container {
      justify-content: unset;
    }
    &__nav {
      margin-right: 20px;
    }
  }
}

.language {
  width: 43px;
  height: 30px;
  display: flex;
  box-shadow: 0px 0px 7px rgba(8, 76, 148, 0.28);
  border-radius: 6px;
  padding: 2px 5px;
  &__uk {
    width: 18px;
  }
  &__arrow-bot {
    width: 15px;
  }
}
