.works {
  padding: 100px 0;
  position: relative;
  background: url("~img/bg-mid.svg") no-repeat;
  background-size: cover;
  &__container {
    display: flex;
    justify-content: center;
  }
  @media screen and (min-width: 1400px) {
    padding: 200px 0;
    background-size: 100% 100%;
  }
}

.works-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 580px;
  &__title {
    margin-bottom: 30px;
  }
  &__card {
    background: #ffffff;
    box-shadow: 0px 0px 12px rgba(63, 146, 209, 0.29);
    border-radius: 6px;
    position: relative;
    padding: 10px 4px 10px 20px;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
  &__circle {
    position: absolute;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: $dark-blue;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-weight: 900;
    font-size: 0.75em; //12px
  }
}

.bg-blue {
  background: $dark-blue;
}

.white-text {
  color: #fff;
}

.example-text {
  color: #3a4d57;
  align-self: flex-end;
  margin-bottom: 10px;
}

.phone-screen {
  position: relative;
  width: 470px;
  display: none;
  &__screen {
    position: absolute;
    top: 60px;
    left: 140px;
    right: 0;
    margin: 0 auto;
    z-index: 1;
  }
  @media screen and (min-width: 1024px) {
    display: unset;
  }
}

.bg-messages {
  position: absolute;
  left: 10%;
}

.bg-mail {
  position: absolute;
  right: 10%;
}

.bg-star {
  position: absolute;
  left: 5%;
  top: 50%;
}
.bg-star2 {
  position: absolute;
  right: 5%;
  top: 50%;
  width: 80px;
}
