.questions {
  padding: 50px 0;
  position: relative;
  background: url("~img/bg-bot.svg") no-repeat;
  background-size: cover;
  &__title {
    margin-bottom: 30px;
  }
  @media screen and (min-width: 768px) {
    padding: 150px 0;
  }
  @media screen and (min-width: 1440px) {
  }
}

.bg-bot {
  position: absolute;
  z-index: -1;
  width: 100%;
}

.questions-block {
  max-width: 850px;
  margin: 0 auto;
  &__item {
    overflow: hidden;
    position: relative;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__btn {
    display: block;
    background: #ffffff;
    border: 1px solid #bdd6e4;
    border-radius: 4px;
    padding: 10px 25px 10px 10px;
    position: relative;
  }
  &__sub {
    display: block;
    background-color: #fff;
    opacity: 0.6;
    padding: 10px;
    transform: translateY(-100%);
    position: absolute;
    z-index: -1;
    &:hover {
      position: relative;
      transform: translateY(0);
      z-index: 1;
    }
  }
}

.questions-block__btn:hover ~ .questions-block__sub {
  position: relative;
  transform: translateY(0);
  z-index: 1;
}

.arrow {
  transform: rotate(180deg) translateY(50%);
  transition: all 0.3s;
  position: absolute;
  right: 10px;
  top: 50%;
}

.questions-block__btn:hover > .arrow {
  transform: rotate(360deg) translateY(-50%);
}
.questions-block__sub:hover > .arrow {
  transform: rotate(360deg) translateY(-50%);
}
