.footer {
  padding: 100px 0;
  @media screen and (min-width: 768px) {
    &__container {
      display: flex;
      justify-content: center;
    }
  }
}

.contact-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @media screen and (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    margin-top: 50px;
    &__item {
      &:not(:last-child) {
        margin-bottom: unset;
        margin-right: 10px;
      }
    }
  }
}

.footer-btns {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  &__title {
    margin-bottom: 30px;
  }
  @media screen and (min-width: 768px) {
    order: 2;
  }
}

.center {
  display: flex;
  align-items: center;
}

.contacts__pic {
  margin-right: 10px;
}
