.coast {
  padding: 50px 0;
  position: relative;
  @media screen and (min-width: 768px) {
    &__container {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  }
}

.cloud-mail {
  width: 110px;
  position: absolute;
  top: 0;
  right: 0;
  @media screen and (min-width: 425px) {
    width: 200px;
  }
  @media screen and (min-width: 768px) {
    position: unset;
    width: 400px;
  }
}

.coast-block {
  max-width: 260px;
  order: 2;
  &__title {
    margin-bottom: 20px;
  }
  &__item {
    display: flex;
    justify-content: flex-start;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__done {
    margin-right: 10px;
  }
  @media screen and (min-width: 1024px) {
    max-width: 400px;
  }
}
